(function () {
  function toggleModalWindow() {
    const html = document.querySelector('html')!;
    const modals = document.querySelectorAll<HTMLElement>('.modal_window[data-modal]');
    const buttons = document.querySelectorAll<HTMLButtonElement>('[data-open-modal]:where(button, input)');

    for (const button of buttons) {
      button.addEventListener('click', function () {
        const name = this.dataset.openModal;
        const modal = document.querySelector(`.modal_window[data-modal="${name}"]`);

        if (!modal) {
          return;
        }

        html.setAttribute('data-modal-visible', 'true');
        modal.setAttribute('data-visible', 'true');
      });
    }

    for (const modal of modals) {
      const close = modal.querySelector('.modal_window__close');
      const backdrop = modal.querySelector('.modal_window__back');

      close?.addEventListener('click', function () {
        modal.setAttribute('data-visible', 'false');
        html.removeAttribute('data-modal-visible');
      });

      backdrop?.addEventListener('click', function () {
        modal.setAttribute('data-visible', 'false');
        html.removeAttribute('data-modal-visible');
      });
    }
  }

  document.addEventListener('DOMContentLoaded', () => {
    toggleModalWindow();
  });
})();
